.share {
  width: 100%;
  max-width: 22rem;
  height: 12.5rem;
  padding: 0 2.5rem;
  margin-bottom: 1rem;
  background-color: var(--neutral-500);
  border-radius: 0.75rem 6rem 0.75rem 0.75rem;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  @media (min-width: 62rem) {
    margin: 0 2rem 0 0;
  }
  &-icons {
    display: flex;
    align-items: center;
    a{
      display: flex;
      align-items: center;
      justify-content: center;
      width: 3rem;
      height: 3rem;
      background-color: var(--neutral-600);
      border-radius: 0.5rem;
      
    }
    * + * {
      margin-left: 1rem;
    }
  }
}

.slider-container {
  position: relative;
  width: 100%;
  max-width: 33.75rem;
  height: 10rem;
  padding: 2rem;
  margin-bottom: 1rem;
  background-color: var(--neutral-500);
  border-radius: 0.75rem;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
 
  @media (min-width: 62rem) {
    margin: 0;
    
  }
    .bar {
      display: inline-block;
      position: absolute;
      z-index: 3;
      left: 2px;
      top: 50%;
      width: 5rem;
      height: 10px;
      border-radius: 5px;
      background-color: #ff0000;
      overflow: hidden;
    }
    .bar, .fill {
      display: block;
      width: 0;
      height: 100%;
      background-color: #6200ee;
    }   
 
  }
  #left{
    color: green;
  }
.bold {
  display: inline-block;
  font-weight: 700;
  width: 6.5ch;
  text-align: right;
} 
.left {
  display: inline-block;
  text-align: right;
  width: 7ch;
  font-weight: 700;
  font-size: larger;
}
.caption {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
  .space-left {
    position: absolute;
    width:max-content;
    top: 100%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    align-items: center;
    font-size: 3rem;
    background-color: var(--neutral-100);
    color: green;
    padding: 1rem;
    border-radius: 0.5rem;
    font-weight: var(--fw-700);
    @media (min-width: 62rem) {
      background-color: var(--neutral-100);
      top: 0%;
      left: 100%;
      transform: translate(calc(-100% - 2rem), calc(-50% - 1rem));
      &::after {
        position: absolute;
        content: '';
        bottom: 0;
        transform: translateY(50%);
        right: 0;
        width: 0;
        height: 0;
        border-top: 1.25rem solid transparent;
        border-bottom: 1.25rem solid transparent;
        border-right: 1.25rem solid var(--neutral-100);
      }
    }
    
    .small {
      font-size: 1rem;
      color:var(--neutral-300);
      text-transform: uppercase;
      margin-left: 0.5rem;
    }

  }
  
  
